@import url(https://fonts.googleapis.com/css?family=Merienda|family=Satisfy&display=swap);
*{
      box-sizing: border-box;
}

body {
 margin:0 ;
  padding:0;
  text-align: center;
  width: 100%;
 height:87vh;
  font-size: 12px;
font-family: 'Satisfy', cursive;
}


.brand{
    font-family: 'Merienda', cursive;
    text-transform: capitalize;
    letter-spacing: 3px;
    background-image: -webkit-linear-gradient(45deg, #f1c40f 10% ,#f39c12 40%, #e74c3c 70%);
    background-image: linear-gradient(45deg, #f1c40f 10% ,#f39c12 40%, #e74c3c 70%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    padding:0 .3rem;
    /* color:white; */
}

/* #f1c40f #f39c12 #e74c3c */

.logo{
    display:flex;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.logo-img{
    width:40px;
    height:40px;
     -webkit-user-select: none;
        -moz-user-select: none;
         -ms-user-select: none;
             user-select: none;
}
#root,.container,.editor,.textarea{
    height:inherit;
}
.container{
     max-width:1100px;
  margin:auto
}
textarea{
    height:95%;
    padding:1.5rem;
    resize: none;
    border:none;
    outline:none;
    background: #2d3436;
    color:white;
    grid-column:1/2;
    overflow-y: auto

}

textarea::-webkit-input-placeholder{
    color:#fab1a0;
}

textarea::-moz-placeholder{
    color:#fab1a0;
}

textarea:-ms-input-placeholder{
    color:#fab1a0;
}

textarea::-ms-input-placeholder{
    color:#fab1a0;
}

textarea::placeholder{
    color:#fab1a0;
}
.editor{

    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(100px,1fr));

    justify-content: center;
    padding:.5rem;

}
.rendered{

    height:95%;
    padding:1.5rem;
     background: #ffb8b8;
    color:#2f3542;
     grid-column: 2/3;
    overflow-wrap: break-word;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: left !important;
    overflow-y:auto;

}
.footer{
    text-transform: capitalize;
   padding-top:3rem;
   bottom:0 !important;
   font-size: 16px;
}


@media only screen and (max-width: 600px) {
    .editor{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
    }
    textarea{
     grid-column: 1/2;
     grid-row:1/3;
 }
 .rendered{
     grid-column: 1/2;
     grid-row:2/3;
 }
}
